<template>
  <div>
    <h1 v-if="info.isFirst">输入用户名搜索</h1>
    <h1 v-if="info.isLoading">加载中...</h1>
    <h1 v-if="info.errorMsg">{{info.errorMsg}}</h1>
    <div class="row">
      <div class="card" v-show="info.users.length" v-for='user in info.users' :key='user.login'>
        <a :href="user.html_url" target="_blank">
          <img :src="user.avatar_url" style='width: 150px'/>
        </a>
        <p class="card-text">{{user.login}}</p>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: "List",
    data(){
      return {
        info:{
          isFirst: true,
          isLoading: false,
          errorMsg: '',
          users: []
        }
      }
    },
    mounted(){
      this.$bus.$on('dataLists',(dataObj)=>{
        // es6的写法，顺序无所谓且不会改变原始数据结构
        this.info = {...this.info,...dataObj}
      })
    }
  }
</script>

<style scoped>
 .row {
   margin-top: 15px;
   margin-left: 30px;
   margin-right: 30px;
 }
 .card {
   display: inline-block;
   text-align:center;
   width: 20%;
   margin-bottom: 3rem;
   background-color: rgb(235, 231, 231);
 }
 .card-text {
   font-size: 85%;
 }
</style>