<template>
  <section class="jumbotron">
    <h3 class="jumbotron-heading">Search Github Users</h3>
    <div>
      <input 
        type="text" 
        placeholder="enter the name you search" 
        v-model="searchName"
        @keyup.enter="searchUsers"
      />
      <button @click="searchUsers">Search</button>
    </div>
  </section>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "Search",
    data() {
      return {
        searchName: ''
      }
    },
    methods:{
        searchUsers(){
            this.$bus.$emit('dataLists',{isFirst:false,isLoading:true,errorMsg:'',users:[]})
            axios.get(`https://api.github.com/search/users?q=${this.searchName}`).then(
                response => {
                    this.$bus.$emit('dataLists',{isLoading:false,errorMsg:'',users:response.data.items})
                },
                error => {
                    this.$bus.$emit('dataLists',{isLoading:false,errorMsg:error.massage,users:[]})
                    console.log("请求出错了")
                }
            )
        }
    }
  }
</script>

